<template>
  <div>
    <v-modal :visible="modalVisible" @hide="modalVisible = false">
      <template v-slot:title>   </template>
      <template v-slot:text
        >        
              (),
             .   
         .
        <v-link>      (gazfond.ru)</v-link>
      </template>
    </v-modal>
    <step-50-50 class="step step-2" :stepNum="2" :amountSteps="7">
      <template v-slot:button-next>
          <button class="form-reg__button _add" @click="next"></button>
      </template>
      <template v-slot:button-prev>
          <button class="form-reg__button _dec" @click="prev"></button>
      </template>
      <template v-slot:title> </template>

      <template v-slot:col1-content>
        <div>
          <tit-h3 v-if="pageWidth > 768"
            >    </tit-h3
          >
          <tit-h3 v-else>   </tit-h3>

          <label for="innRFY" class="mb-4">
            <v-text class="fw-bold text-start">
              <custom-input class="me-2">
                <input type="radio" checked name="innRF" id="innRFY" />
              </custom-input>
                  
            </v-text>
          </label>

          <div class="d-flex flex-wrap mb-3">
            <div class="input-wrapper mb-2 col-md-12 col-6">
              <v-input :placeholder="INNplaceholder"></v-input>
            </div>
            <div class="text-end mb-md-4 col-md-12 col-6">
              <v-link>    </v-link>
            </div>
          </div>

          <v-text>
            <label class="v-text">
              <custom-input class="me-2">
                <input type="checkbox" name="" id="" />
              </custom-input>

              ,      
            </label>
          </v-text>
        </div>
      </template>
      <template v-slot:col2-content>
        <div class="form-reg__disabled">
          <tit-h3 v-if="pageWidth > 768"
            >   </tit-h3
          >
          <tit-h3 v-else>  </tit-h3>

          <label for="innRFN" class="d-flex">
            <custom-input class="me-3">
              <input
                type="radio"
                name="innRF"
                id="innRFN"
                @change="openModal"
              />
            </custom-input>
            <v-text class="mt-1">
                  
            </v-text>
            
          </label>
        </div>
      </template>
    </step-50-50>
  </div>
</template>

<script>
export default {
  data() {
    return {
      pageWidth: window.innerWidth,
      modalVisible: false,
      INNplaceholder:
        window.innerWidth > 768
          ? "   ()"
          : "",
    };
  },
  methods: {
    openModal() {
      this.modalVisible = true;
    },
    next() {
      this.$emit('next')
    },
    prev() {
      this.$emit('prev')
    }
  },
};
</script>

