<template>
  <button class="v-button">
    <slot></slot>
  </button>
</template>

<script>
export default {
  name: 'v-button'
};
</script>

<style lang="scss">
.v-button {
  font-family: "Inter", sans-serif;
  font-weight: 700;
  padding: 10px 0;
  text-align: center;
  background: #fff;
  color: rgba(0, 0, 0, 0.7);
  border: none;
  transition: 0.1s;
  background: #0275b7;
  color: #fff;

  @media (max-width: 567px) {
    padding: 7px 0;
    text-align: center;
    font-size: 12px;
    font-weight: 500;
  }
}
.v-button._add {
  background: #0087d4;
  color: #fff;
}
.v-button._add:hover,
.v-button._add:focus {
