<template>
  <div
    class="step step-7"
    :class="{
      'visible-1': visibleBlock === 1,
      'visible-2': visibleBlock === 2,
    }"
  >
    <step-50-50 :stepNum="7" :amountSteps="7">
      <template v-slot:button-prev>
          <button class="form-reg__button _dec" @click="prev"></button>
      </template>

      <template v-slot:title>   </template>

      <template v-slot:col1-content>
        <div>
          <tit-h3>  </tit-h3>
          <v-text>
                ,  
              <v-link> </v-link>  
             :
          </v-text>

          <ul>
            <v-link>
              <li>  ;</li>
            </v-link>
            <v-link>
              <li> ;</li>
            </v-link>
            <v-link>
              <li> .</li>
            </v-link>
          </ul>

          <label for="confirmation" class="d-flex mb-3">
            <custom-input class="col-1 me-4">
              <input type="checkbox" name="" id="confirmation" />
            </custom-input>
            ,  ,  ,   
            .    ()   
             .
          </label>
          <div class="text-end">
            <button class="ps-3 pe-3 add v-button _add" @click="sendCode" 
            :disabled="visibleBlock === 2">
               SMS-
            </button>
          </div>
        </div>
      </template>

      <template v-slot:col2-content>
        <div>
          <tit-h3>   </tit-h3>
          <v-text class="mb-4">
            SMS-     
                   
            
          </v-text>

          <b-row class="me-0 ms-0 mb-4">
            <v-input
              placeholder=" SMS- "
              class="col-lg-7 col-6 input-confirmation"
            >
            </v-input>
            <div class="col-lg-5 col-6 d-flex justify-content-end p-0">
                <button class="_add col-md-11 col-12 v-button" :disabled="visibleBlock === 1" @click="next">
                  
                </button>
            </div>
          </b-row>
          <v-text class="mb-4">
                SMS-      :
          </v-text>
          <v-text v-if="telTimer > 0" class="fw-bold m-0"
            >{{ telTimer }} </v-text
          >
          <button v-else class="v-link text-start" @click="sendCode">
              
          </button>
        </div>
      </template>
    </step-50-50>
  </div>
</template>

<script>
export default {
  data() {
    return {
      visibleBlock: 1,
      pageWidth: window.innerWidth,
      telTimer: 5,
    };
  },
  methods: {
    startTelTimer() {
      let timer = setInterval(() => {
        if (this.telTimer > 0) {
          this.telTimer--;
        } else {
          clearInterval(timer);
        }
      }, 1000);
    },
    sendCode() {
      this.visibleBlock = 2;
      this.telTimer = 5;
      this.startTelTimer();
    },
    prev() {
      this.$emit("prev");
    },
    next() {
      this.$emit("next");
    },
  },
};
</script>

<style lang="scss">
@media (min-width:768px) {
  .step-7.visible-1 {
  .from-reg__col2 {
    position: relative;
    opacity: .7;
    &:after {
      content: '';
      z-index: 2;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
  .from-reg__col1 {
    opacity: 1;
    &:after {
      display: none;
    }
  }
}
.step-7.visible-2 {
  .from-reg__col2 {
    opacity: 1;
    &:after {
      display: none;
    }
  }
  .from-reg__col1 {
    position: relative;
    opacity: .7;
    &:after {
      content: '';
      z-index: 2;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}
}
@media (max-width: 768px) {
  .step-7.visible-1 {
    .from-reg__col2 {
      display: none !important;
    }
    .from-reg__col1 {
      display: block !important;
    }
  }
  .step-7.visible-2 {
    .from-reg__col2 {
      display: block !important;
    }
    .from-reg__col1 {
      display: none !important;
    }
  }
}
</style>